<script>
import { ref, reactive } from 'vue';

export default{
  setup(){
    // const / let

    const a = reactive({
      name: 'Benjamin'
    })
    // a.name = 'Tommy'

    let b = {
      name: 'Ted'
    }
    // a = b;
